import imageLibrary from '../data/images';
import styles from '../css/Info.module.css';

export default function About() {

    return (
        <section>
            <div className={styles.container}>
                <h3 style={{ margin: '3rem auto 0' }}>?  !</h3>
                <div>             
                    <img
                        src={imageLibrary.find(img => img.id === 1).src}
                        alt={imageLibrary.find(img => img.id === 1).alt}
                        style={{ width: '200px' }}
                    />
                </div>

                <h3><strong style={{color: "var(--color-notice)"}}>C2B2C</strong><em style={{color: "#888"}}>(Consumer to Business to Consumer)</em></h3>
                <p style={{marginTop:"-37px", color: "#999"}}>--    </p>

                {/* <div className={styles.info}>
                    <p><span>One by One</span>  </p>
                    <p>  .</p>
                </div> */}

                <h3>  </h3>

                <div className={styles.info}>
                    <p>    ,</p>
                    <p>   <span>7 </span> .</p>
                </div>

                <div className={styles.info}>
                    <p>    <span>7 </span> ,</p>
                    <p>( )   <span>7 </span> .</p>
                    <p style={{ color: 'var(--color-notice)' }}><span></span>    .</p>
                </div>

                <div className={styles.info}>
                    <p>    ,    ,</p>
                    <p>    .</p>
                </div>

                <h3 className={styles.beforeSunrise}> </h3>
                <ul className={styles.riseInfo}>
                    <li><strong>    /</strong>             .</li>
                    <li><strong>   /</strong>   , <em>( )</em>   . -     </li>
                    <li><strong>  /</strong>        .</li>
                    <li><strong>  /</strong>                .</li>
                </ul>
            </div>
        </section>
    );
}