import { useState } from 'react';
import axios from '../api/axios';
import { getFeeSummary } from '../utils/feeSummary';
import TransactionNotice from './TransactionNotice';
import styles from '../css/Posts.module.css';

export default function PostRegister({ onPostCreated, onCancel }) {
  const [type, setType] = useState('seller');
  const [title, setTitle] = useState('');
  const [price, setPrice] = useState('');
  const [link, setLink] = useState('');
  const [feeResponsibility, setFeeResponsibility] = useState('buyer');
  const [paymentMethod, setPaymentMethod] = useState('cash');
  const [error, setError] = useState('');
  const token = localStorage.getItem('token');

  const generateMatchCode = () =>
    Math.random().toString(36).substring(2, 8).toUpperCase();

  const normalizeLink = url => {
    if (!/^https?:\/\//i.test(url)) {
      return 'http://' + url;
    }
    return url;
  };

  const handleSubmit = async () => {
    if (!title || !price || !link) {
      setError('  .');
      return;
    }

    const normalizedLink = normalizeLink(link);

    try {
      const matchCode = generateMatchCode();
      const summary = getFeeSummary({ price, feeResponsibility, paymentMethod });

      const res = await axios.post('/posts', {
        type,
        title,
        price: parseInt(price, 10),
        link: normalizedLink, //    
        matchCode,
        feeResponsibility,
        paymentMethod,
        buyerAmount: summary.buyerAmount,
        sellerAmount: summary.sellerAmount,
        totalAmount: summary.totalAmount,
        feeAmount: summary.feeAmount
      }, {
        headers: { Authorization: `Bearer ${token}` }
      });

      onPostCreated(res.data);
      setTitle('');
      setPrice('');
      setLink('');
      setFeeResponsibility('buyer');
      setPaymentMethod('cash');
      setError('');
    } catch (err) {
      setError(' .  .');
    }
  };

  const handleCancel = () => {
    setTitle('');
    setPrice('');
    setLink('');
    setFeeResponsibility('buyer');
    setPaymentMethod('cash');
    setError('');
    if (onCancel) onCancel();
  };

  const summary = getFeeSummary({ price, feeResponsibility, paymentMethod });

  return (
    <section>
    <div className={styles.card}>
      <h3>  </h3>

      <p className={styles.small_noti}> () ()   .</p>
      <p className={styles.small_sub}>    7  .</p>

      {/*   */}
      <div className={styles.radioGroup}>
        <label><input type="radio" value="seller" checked={type === 'seller'} onChange={() => setType('seller')} /> </label>
        <label><input type="radio" value="buyer" checked={type === 'buyer'} onChange={() => setType('buyer')} /> </label>
      </div>

      {/*    */}
      <input type="text" placeholder="" value={title} onChange={e => setTitle(e.target.value)} className={styles.input} />
      <input type="number" min={50000} step={1000} placeholder="  50,000 1,000 ." value={price} onChange={e => setPrice(e.target.value)} className={styles.input} />
      <input type="text" placeholder="   (: naver.com/item/123)" value={link} onChange={e => setLink(e.target.value)} className={styles.input} />

      {/*    */}
      <div className={styles.radioGroup}>
        <label><input type="radio" value="buyer" checked={feeResponsibility === 'buyer'} onChange={() => setFeeResponsibility('buyer')} />  </label>
        <label><input type="radio" value="seller" checked={feeResponsibility === 'seller'} onChange={() => setFeeResponsibility('seller')} />  </label>
        <label><input type="radio" value="split" checked={feeResponsibility === 'split'} onChange={() => setFeeResponsibility('split')} />  </label>
      </div>

      {/*    */}
      <div className={styles.radioGroup}>
        <label><input type="radio" value="cash" checked={paymentMethod === 'cash'} onChange={() => setPaymentMethod('cash')} />  </label>
        <label><input type="radio" value="pay" checked={paymentMethod === 'pay'} onChange={() => setPaymentMethod('pay')} />  </label>
      </div>

      {/*   */}
      {summary && (
        <pre className={styles.feeNotice}>
          <p>: {summary.feeAmount.toLocaleString()}</p>
          <p>  : {summary.buyerAmount.toLocaleString()}</p>
          <p>  : {summary.sellerAmount.toLocaleString()}</p>
          {/* <p>  : {summary.totalAmount.toLocaleString()}</p> */}
        </pre>
      )}

      {/*    */}
      <TransactionNotice />

      <div className={styles.buttonGroup}>
        <button onClick={handleSubmit} className={styles.registerBtn}></button>
        <button onClick={handleCancel} className={styles.cancelBtn}></button>
      </div>

      {error && <p className={styles.error}>{error}</p>}
    </div>
    </section>
  );
}