import { useParams, useNavigate } from 'react-router-dom';
import PostViewer from '../components/PostViewer';

export default function PostDetailPage({ user }) {
  const { id } = useParams(); // URL  ID 
  const navigate = useNavigate();

  //       
  if (!user || !user._id || !user.role) {
    return (
      <div style={{ padding: '2rem', textAlign: 'center' }}>
        <h2>    .</h2>
        <p>   .</p>
      </div>
    );
  }

  //  ID   (: 'admin'  )
  const isInvalidId = !id || id.length < 12 || id === 'admin'; // ObjectId  12
  if (isInvalidId) {
    return (
      <div style={{ padding: '2rem', textAlign: 'center' }}>
        <h2>   ID.</h2>
        <p>   . <button onClick={() => navigate('/')}></button></p>
      </div>
    );
  }

  return (
    <section>
      <h2 style={{ marginBottom: '20px' }}> </h2>
      <PostViewer
        postId={id}                //  ID 
        userRole={user.role}       // 'user'  'admin'
        currentUserId={user._id}   //   ID
      />
    </section>
  );
}
