import { useEffect, useState } from 'react';
import axios from '../api/axios';
import styles from '../css/Dashboard.module.css';

export default function DashboardStats() {
  const [stats, setStats] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    axios.get('/admin/stats')
      .then(res => {
        setStats(res.data);
        setLoading(false);
      })
      .catch(err => {
        console.error('    :', err);
        setError('    .');
        setLoading(false);
      });
  }, []);

  if (loading) {
    return <div className={styles.statsBox}>    ...</div>;
  }

  if (error) {
    return <div className={styles.statsBox}> {error}</div>;
  }

  return (
    <div className={styles.statsBox}>
      <ul className={styles.statsList}>
        <li> : <strong>{stats.totalUsers}</strong></li>
        <li> : <strong>{stats.totalPosts}</strong></li>
        <li> : <strong>{stats.matchedPosts}</strong></li>
        <li> : <strong>{stats.shippingInProgress}</strong></li>
        <li> : <strong>{stats.completedTransactions}</strong></li>
        {/* <li>  : <strong>{stats.bannedUsers}</strong></li> */}
      </ul>
    </div>
  );
}