import { useState } from 'react';
import styles from '../css/Posts.module.css';

export default function AdminActions({ post, updatePost }) {
  const [inspectionResult, setInspectionResult] = useState(post.inspectionResult || '');
  const [description, setDescription] = useState(post.inspectionDescription || '');
  const [courier, setCourier] = useState(post.shipping?.courier || '');
  const [tracking, setTracking] = useState(post.shipping?.tracking || '');

  const { buyerStatus, sellerStatus, inspectionResult: savedResult } = post;

  //    
  const updateStatus = async (type, nextStatus, extra = {}) => {
    const patch = type === 'buyer'
      ? { buyerStatus: nextStatus }
      : { sellerStatus: nextStatus, ...extra };

    try {
      await updatePost(patch);
      alert(`${type === 'buyer' ? ' ' : ' '}  '${nextStatus}' .`);
    } catch (err) {
      alert(`${type === 'buyer' ? '' : ''}   : ${err.response?.data?.message || ' '}`);
    }
  };

  //    
  const handleInspectionSubmit = async () => {
    if (!inspectionResult) return alert('  .');
    if (inspectionResult === '' && !description.trim()) {
      return alert('   .');
    }

    try {
      await updatePost({
        inspectionResult,
        inspectionDescription: inspectionResult === '' ? description : ''
      });
      alert('   .');
    } catch (err) {
      alert('   : ' + (err.response?.data?.message || ' '));
    }
  };

  //      
  const handleShippingSubmit = async () => {
    if (!courier.trim() || !tracking.trim()) {
      return alert('   .');
    }

    try {
      await updatePost({
        shipping: {
          courier,
          tracking,
          dispatchedAt: new Date()
        },
        buyerStatus: ''
      });
      alert('   ');
    } catch (err) {
      alert('  : ' + (err.response?.data?.message || ' '));
    }
  };

  return (
    <div className={styles.adminBox}>
      <h4> </h4>

      {/*    */}
      <div className={styles.subBox}>
        <h5> </h5>
        {buyerStatus === '' && (
          <button onClick={() => updateStatus('buyer', '')}>  </button>
        )}
        {buyerStatus === '' && (
          <button onClick={() => updateStatus('buyer', '')}>   </button>
        )}
        {buyerStatus === '' && savedResult !== null && (
          <button onClick={() => updateStatus('buyer', '')}>   </button>
        )}
        {buyerStatus === '' && (
          <div className={styles.subBox}>
            <h5>  </h5>
            <input
              type="text"
              placeholder=""
              value={courier}
              onChange={(e) => setCourier(e.target.value)}
              className={styles.input}
            />
            <input
              type="text"
              placeholder=""
              value={tracking}
              onChange={(e) => setTracking(e.target.value)}
              className={styles.input}
            />
            <button onClick={handleShippingSubmit}>  </button>
          </div>
        )}
        {buyerStatus === '' && (
          <button onClick={() => updateStatus('buyer', '')}>  </button>
        )}
      </div>

      {/*    */}
      <div className={styles.subBox}>
        <h5> </h5>
        {sellerStatus === '' && (
          <button onClick={() => updateStatus('seller', '', {
            shipping: { courier: '', tracking: '', dispatchedAt: null }
          })}>
               ( )
          </button>
        )}
        {sellerStatus === '' && (
          <button onClick={() => updateStatus('seller', '')}>   </button>
        )}
        {sellerStatus === '' && (
          <>
            <label>
                 :
              <select
                value={inspectionResult}
                onChange={(e) => setInspectionResult(e.target.value)}
                className={styles.input}
              >
                <option value=""></option>
                <option value="">  </option>
                <option value="">     </option>
              </select>
            </label>

            {inspectionResult === '' && (
              <textarea
                placeholder="     "
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                className={styles.textarea}
              />
            )}

            <button onClick={handleInspectionSubmit} className={styles.confirmBtn}>
                 
            </button>

            {savedResult !== null && (
              <button onClick={() => updateStatus('seller', '')}>  </button>
            )}
          </>
        )}
        {sellerStatus === '' && (
          <button onClick={() => updateStatus('seller', '')}>  </button>
        )}
      </div>
    </div>
  );
}