import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from '../api/axios';
import styles from '../css/Form.module.css';

export default function LoginForm({ onLogin }) {
  const [form, setForm] = useState({ phone: '', password: '' });
  const [error, setError] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const navigate = useNavigate();

  //    :     
  const checkUserPosts = async (token) => {
    try {
      const res = await axios.get('/posts/my', {
        headers: { Authorization: `Bearer ${token}` }
      });

      const posts = res.data;

      const hasActivePost = posts.some(post => {
        const matched = post.matcher !== null;
        const buyerDone = post.buyerStatus === '';
        const sellerDone = post.sellerStatus === '';
        return matched && (!buyerDone || !sellerDone);
      });

      if (!hasActivePost) {
        await axios.patch('/users/mark-inactive', {}, {
          headers: { Authorization: `Bearer ${token}` }
        });
        console.log('    (   )');
      }
    } catch (err) {
      console.warn('    :', err.message);
    }
  };

  const handleChange = e => {
    const { name, value } = e.target;
    setForm(prev => ({ ...prev, [name]: value }));
    setError('');
  };

  const handleSubmit = async e => {
    e.preventDefault();
    try {
      const res = await axios.post('/auth/login', form);
      const { token, user } = res.data;

      localStorage.setItem('token', token);
      onLogin(user);
      navigate('/');

      await checkUserPosts(token);
    } catch (err) {
      const msg = err.response?.data?.message;
      if (msg === '  .') {
        setError(msg); //  
      } else if (msg === ' .') {
        setError(msg);
      } else {
        setError(msg || '   .');
      }
    }
  };

  return (
    <form onSubmit={handleSubmit} className={styles.form}>
      <section>
        <h2></h2>

        <div className={styles.regFormbox}>
          <input
            type="text"
            name="phone"
            placeholder=""
            value={form.phone}
            onChange={handleChange}
            required
            className={styles.input}
          />

          <div className={styles.passwordWrapper}>
            <input
              type={showPassword ? 'text' : 'password'}
              name="password"
              placeholder=""
              value={form.password}
              onChange={handleChange}
              required
              className={styles.input}
            />
            <button
              type="button"
              onClick={() => setShowPassword(prev => !prev)}
              className={styles.toggle}
            >
              {showPassword ? ' ' : ' '}
            </button>
          </div>

          {error && <div className={styles.error}>{error}</div>}

          <button type="submit"></button>
        </div>
      </section>
    </form>
  );
}