import { useState } from 'react';
import styles from '../css/Posts.module.css';
import TrackingStatus from './TrackingStatus';
import imageLibrary from '../data/images';

export default function BuyerActions({ post, updatePost }) {
  const [paymentMethod, setPaymentMethod] = useState(post.paymentMethod || '');
  const [trackingInfo, setTrackingInfo] = useState(null);

  const { buyerStatus, inspectionResult, shipping } = post;

  //   
  const canRequestDeposit = !buyerStatus || buyerStatus === ' ';
  const isWaitingForInspection = ['', ''].includes(buyerStatus) && inspectionResult === null;
  const canDecisionOnInspection = buyerStatus === '' && inspectionResult !== null;
  const canTrackShipping = buyerStatus === '' && shipping?.courier && shipping?.tracking;
  const canFinalize = buyerStatus === '';

  //    
  const handleDepositRequest = () => {
    if (!paymentMethod) {
      alert('  .');
      return;
    }
    updatePost({ buyerStatus: '', paymentMethod: 'cash' });
  };

  //   /  
  const handlePurchaseConfirm = () => updatePost({ buyerStatus: '' });
  const handleCancelPurchase = () => updatePost({ buyerStatus: '' });

  //   
  const handleFinalize = () => updatePost({ buyerStatus: '' });

  //   
  const fetchTracking = async () => {
    try {
      const res = await fetch(`/tracking?code=${shipping.courier}&invoice=${shipping.tracking}`);
      const data = await res.json();
      setTrackingInfo(data);
    } catch (err) {
      alert('  : ' + (err.message || 'API '));
    }
  };

  return (
    <div className={styles.actionBox}>
      <h4> </h4>

      {/*        */}
      {canRequestDeposit && (
        <>
          {/* <select
            value={paymentMethod}
            onChange={e => setPaymentMethod(e.target.value)}
            className={styles.input}
          >
            <option value="">  </option>
            <option value="cash"> </option>
            <option value="pay"></option>
          </select> */}

          {paymentMethod === 'cash' && (
            <div className={styles.notice}>
              <p><img
                  src={imageLibrary.find(img => img.id === 2).src}
                  alt={imageLibrary.find(img => img.id === 2).alt}
                  className={styles.notiBank}
                />
                <span onClick={() => {navigator.clipboard.writeText("100041715312");alert('  ');
              }}> 1000-4171-5312</span> 
              </p>
              <p style={{ color: "#d65c5cff", marginLeft: "16px", marginBottom: "5px" }}><span style={{ fontSize: "12px" }}></span>  ""  !</p>{' '}
              <button onClick={handleDepositRequest} className={styles.confirmBtn}></button>
            </div>
          )}

          {post.paymentMethod === 'pay' && post.smartstoreProductId && (
            <a
              href={`https://smartstore.naver.com/grinform/products/${post.smartstoreProductId}`}
              target="_blank"
              rel="noopener noreferrer"
              className={styles.confirmBtn}
            >
              
            </a>
          )}
        </>
      )}

      {/*      */}
      {isWaitingForInspection && (
        <div className={styles.notice}>
              .
        </div>
      )}

      {/*       */}
      {canDecisionOnInspection && (
        <div className={styles.notice}>
          <p>   : <strong>{inspectionResult}</strong></p>
          {post.inspectionDescription && (
            <p className={styles.descriptionBox}>
              <strong>:</strong> {post.inspectionDescription} <br />
              <span className={styles.small_noti}>    .</span>
            </p>
          )}
          <div className={styles.buttonGroup}>
            <button onClick={handlePurchaseConfirm} className={styles.confirmBtn}> </button>
            <button onClick={handleCancelPurchase} className={styles.confirmBtn}> </button>
          </div>
        </div>
      )}

      {/*    */}
      {canTrackShipping && (
        <div className={styles.buttonGroup}>
          <button onClick={fetchTracking}>  </button>
        </div>
      )}

      {/*     */}
      {trackingInfo && <TrackingStatus trackingInfo={trackingInfo} />}

      {/*    */}
      {canFinalize && (
        <button onClick={handleFinalize} className={styles.confirmBtn}>
           
        </button>
      )}
    </div>
  );
}